
@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');

* {
  font-family: 'Roboto', sans-serif;

}




a{
  font-size: calc(0.5vw + 8px);
  text-decoration: none;
  color: rgb(175, 175, 175);
  outline: none;
  padding: 8px;
  border: 1px solid rgba(0, 0, 0, 0);
  transition-duration: 1000ms;
}

a:focus{
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 8px;
  border: 1px solid black;
  color: black;
}

.navigation{
  bottom: 28px;
  left: 15px;
  position: fixed;
  color: rgb(121, 121, 121);
  z-index: 1;
}


.info1, .info2{
  font-size: calc(8px + 1vh);
  font-weight: 1000;
  position: absolute;
  left: 1vh;
  border-radius: 5%;
  padding: 1vh;
  top: 0;
}

.info1{
  background-color: rgba(0, 0, 0, 0);
  color: rgba(0, 0, 0, 0);
  transition-duration: 200ms;
  z-index: 2;
}

.info2{
  background-color: rgba(0, 0, 0, 0.219);
  color: rgb(255, 255, 255);
  pointer-events: none;
  z-index: 1;
}

.info1:hover{
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
}

/* MOSAIC */

.mosaicApp {

  z-index: 1;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  background-color: rgb(0, 0, 0);
  overflow: hidden;
  transition-duration: 400ms;

}

.mosaicElement{
  background-color: rgb(255, 255, 255);
  width: 10vh;
  height: 10vh;
  transition: background-color 22000ms ease, width 400ms, height 400ms, border 400ms, border-radius 4000ms, transform 2000ms ease;
  box-sizing: border-box;
  border: 3vh solid rgb(255, 255, 255);
}

@media only screen and (max-width: 800px) {
  body .mosaicElement:active{
    background-color: rgb(0, 0, 0);
    width: 10vh;
    height: 10vh;
    transition: background-color 100ms ease, width 400ms, height 400ms, border 100ms, transform 500ms ease;
    border: 0px solid black;
  }
}

@media only screen and (min-width: 800px) {
  body .mosaicElement:hover{
    background-color: rgb(0, 0, 0);
    width: 10vh;
    height: 10vh;
    transition: background-color 100ms ease, width 400ms, height 400ms, border 100ms, transform 500ms ease;
    border: 0px solid black;
  }
}

/* LACERATIONS */

.lacerationsApp {
  height: 100%;
  display: flex;
  justify-content: stretch;
  background: linear-gradient(120deg, rgb(255, 115, 0) 0%, rgb(0, 247, 255) 35%, rgb(255, 202, 28) 100%);
  background-color: black;
  align-items: stretch;
  align-content: stretch;
  overflow: hidden;
}

.lacerationsElement{
  background-color: rgb(0, 0, 0);
  width: 10vw;
  height: 100vh;
  transition: width 5000ms ease, background-color 5000ms ease;
}

@media only screen and (max-width: 800px) {
  .lacerationsElement:active{
    background-color: rgba(255, 0, 0, 0);
    transition: background-color 500ms ease, width 500ms ease;
    width: 30vw;
  }
}

@media only screen and (min-width: 800px) {
  .lacerationsElement:hover{
    background-color: rgba(255, 0, 0, 0);
    transition: background-color 500ms ease, width 500ms ease;
    width: 30vw;
  }
}
/* PEEPSHOW */

.peepshowApp {
  z-index: 2;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  background: linear-gradient(130deg, rgb(0, 255, 200) 0%, rgb(229, 255, 0) 35%, rgb(255, 0, 191) 100%);
  overflow: hidden;
  align-items: stretch;
  align-content: stretch;
  justify-content: stretch;
}

/* .peepshowApp:active > .peepshowElement {
  background-color: rgb(0, 0, 0);
  transition: border-radius 500ms ease, background-color 500ms ease;
  border-radius: 30%;
} */



@media only screen and (min-width: 800px) {
  .peepshowElement:hover{
    background-color: rgb(0, 0, 0);
    width: 10vh;
    height: 10vh;
    transition: border-radius 500ms ease, background-color 500ms ease;
    border-radius: 30%;
  }

  .peepshowElement{
    background-color: rgb(0, 0, 0);
    width: 10vh;
    height: 10vh;
    transition: border-radius 5000ms ease;
  }
  
}

@media only screen and (max-width: 800px) {
  .peepshowElement:active{
    background-color: rgb(0, 0, 0);
    width: calc(100vh/8);
    height: calc(100vh/8);
    transition: border-radius 400ms ease, background-color 500ms ease;
    border-radius: 40%;
  }

  .peepshowElement{
    background-color: rgb(0, 0, 0);
    width: calc(100vh/8);
    height: calc(100vh/8);
    transition: border-radius 8000ms ease;
  }
  
}
.peepshowElement:active{
  background-color: rgb(0, 0, 0);
}

/* MOSES */

.mosesApp {
  height: 100vh;
  display: flex;
  justify-content: stretch;
  background-color: rgb(0, 0, 0);
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  overflow: hidden;
  transition-duration: 2000ms;
}

.mosesElement{
  background-color: rgb(255, 255, 255);
  width: 5vw;
  height: 100vh;
  transition: height 10000ms ease, border-radius 1000ms ease, background-color 1000ms ease, margin-top 400ms ease, margin-bottom 400ms ease;
  border: 0px solid rgb(0, 0, 0);
  border-radius: 12%;
  box-shadow: inset 0px 0px 0px px rgb(0, 0, 0);
  box-sizing: border-box;
  margin-top: 1px;
  margin-bottom: 1px;
}

@media only screen and (min-width: 800px) {
  .mosesElement:hover{
    background-color: rgba(0, 0, 0, 0);
    width: 5vw;
    height: 200px;
    transition: height 500ms ease, border-radius 500ms ease, background-color 500ms ease;
    border-radius: 45%;
    border: 0px solid black;
  }
  .mosesApp:active > .mosesElement{
    transition-duration: 500ms;
    transition: height 500ms ease, border-radius 500ms ease, background-color 3000ms ease, margin-top 2000ms ease, margin-bottom 2000ms ease;
    border-radius: 20%;
    margin-top: 200px;
    margin-bottom: 200px;
  }
  .mosesApp:active {
    background: rgb(255, 0, 0);
  }
}

@media only screen and (max-width: 800px) {
  .mosesElement:active{
    background-color: rgba(0, 0, 0, 0);
    width: 10vw;
    height: 200px;
    transition: height 500ms ease, border-radius 500ms ease, background-color 500ms ease;
    border-radius: 45%;
    border: 0px solid black;
  }
  .mosesElement{
    width: 10vw;
  }
}

/* UNTITLED */

.untitledApp {
  z-index: 2;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: stretch;
  /* background-color: rgb(255, 255, 255); */
background-color: green;
  overflow: hidden;
  transition-duration: 500ms;
  background: linear-gradient(45deg, rgb(255, 208, 0) 0%, rgb(72, 255, 0) 50%, rgb(17, 0, 255) 100%);

}

.untitledElement{
  width: 100vw;
  height: 300vh;
  transition: height 5000ms ease, background-color 5000ms ease, filter 2000ms ease, margin 1000ms ease;
  /* border: 1px solid black; */
  background: linear-gradient(45deg, rgb(0, 255, 191) 0%, rgb(0, 225, 255) 25%,  rgb(255, 0, 242) 50%,  rgb(255, 136, 0) 75%, rgb(0, 255, 179) 100%);
  margin-top: -0vh;
  filter: blur(0px);
}



@media only screen and (min-width: 800px) {
  .untitledElement:active{
    background-color: rgb(0, 0, 0);
    transition: background-color 5000ms ease, height 5000ms ease, filter 500ms ease, margin 500ms ease;
    filter: blur(0px);
    margin-top: -200vh;
  }

  .untitledElement2{
    z-index: 1;
    width: 20vw;
    height: 20vw;
    position: fixed;
    left: 40vw;
    top: 30vh;
    transition-duration: 500ms;
    background: linear-gradient(90deg,  rgba(255, 0, 179, 0) 40%,  rgba(255, 255, 255, 0.555) 90%);
    /* background-color: rgba(255, 255, 255, 0.479); */
    filter: blur(0px);
    border-radius: 100%;
  }

  .untitledApp:active > .untitledElement2{
    z-index: 1;

    background-color: rgba(255, 255, 255, 0);
    position: fixed;
    transform: rotateZ(360deg);
    transition-duration: 800ms;
    filter: blur(30px);
    border-radius: 100%;
  }
}

@media only screen and (max-width: 800px) {
  .untitledElement:active{
    background-color: rgb(0, 0, 0);
    transition: background-color 5000ms ease, height 5000ms ease, filter 500ms ease, margin 500ms ease;
    filter: blur(0px);
    margin-top: -200vh;
  }

  .untitledElement2{
    z-index: 1;
    width: 40vw;
    height: 40vw;
    position: fixed;
    left: 30vw;
    top: 30vh;
    transition-duration: 500ms;
    background: linear-gradient(90deg,  rgba(255, 0, 179, 0) 40%,  rgba(255, 255, 255, 0.555) 90%);
    /* background-color: rgba(255, 255, 255, 0.479); */
    filter: blur(0px);
    border-radius: 100%;
  }

  .untitledApp:active > .untitledElement2{
    z-index: 1;
    width: 40vw;
    height: 40vw;
    background-color: rgba(255, 255, 255, 0);
    position: fixed;
    left: 30vw;
    top: 30vh;
    transform: rotateZ(360deg);
    transition-duration: 800ms;
    filter: blur(0px);
    border-radius: 100%;
  }
}

